.main {
  height: 100vh;
  width: 100vw;
  background-color: black;
  display: flex;
  flex-direction: column;
}

.loader {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8px;
    position: absolute;
    width: 100%;
    background-color: black;
    top: 0;
}
.loader p{
    color: white;
    margin: 8px 12px;
}
.video-container{
    flex-grow: 1;
}
.video-container video{
    width: 100%;
    height: 100%;
}
.toolbar {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.btn {
  margin: 0 12px !important;
  min-width: 100px !important;
}


/* ovverride */
.MuiCircularProgress-colorPrimary {
    color: white !important;
}
.MuiIconButton-colorSecondary{
    padding: 16px !important;
}


@media (orientation: landscape) {
  .main {
    flex-direction: row;
    align-items: flex-end;
  }
  .video-container{
    flex-grow: 1;
    height: 100%;
    width: 100%;
}
  .toolbar {
    width: auto;
  }
  .toolbar div{
    display: flex;
    flex-direction: column;
    width: auto;
  }
}